#page-header .mdl-layout-title img {
  width: 80px;
  height: 80px; }

#page-header .mdl-layout-title a h1 {
  font-size: x-large;
  text-decoration: none;
  color: #3f51b5;
  margin-left: 20px;
  display: inline-block;
  font-weight: 550; }

#page-header .demo-layout-transparent .mdl-layout__header {
  color: #3f51b5; }

#page-header .mdl-layout__header-row {
  height: 90px;
  padding-left: 10px;
  border-bottom: 1px solid #3f51b5; }
  #page-header .mdl-layout__header-row .mdl-navigation__link {
    font-size: 90%;
    font-weight: bold; }

#page-header header {
  height: 120px; }

.demo-layout-transparent .mdl-layout__drawer-button {
  color: #3f51b5; }

@media screen and (max-width: 1024px) {
  #page-header .mdl-layout__header {
    display: block; } }

@media screen and (max-width: 768px) {
  #page-header .mdl-layout__header {
    display: none; } }



