#about-us-submenu .demo-list-item .mdl-list__item-primary-content {
  display: inline-block;
  text-align: center;
  padding-right: 15px; }

#contacts-page .demo-card-square {
  display: inline-block;
  margin: 15px; }
  #contacts-page .demo-card-square.mdl-card {
    width: 320px;
    height: 380px; }
  #contacts-page .demo-card-square > .mdl-card__title {
    color: #fff;
    height: 250px; }

#contacts-page .mdl-card__title-text,
#contacts-page .mdl-card__supporting-text {
  text-align: center;
  margin: auto; }

#contacts-page .mdl-card__title-text {
  font-size: 24px; }

#contacts-page .mdl-card__supporting-text {
  font-size: 18px; }

#contacts-page .mdl-textfield--floating-label .mdl-textfield__label {
  width: 100%; }

#contacts-page .mdl-dialog {
  width: 700px; }

#contacts-page .mdl-textfield {
  width: 100%; }

#contacts-page h1 {
  text-align: center; }

#contacts-page div label {
  width: 25%; }

#contacts-page .mdl-list__item .material-icons {
  margin-right: 10px;
  display: none; }

#question {
  color: #3f51b5; }

#contacts-page .mdl-button--accent.mdl-button--accent:hover {
  background-color: #3f51b5;
  color: #ffffff; }

@media screen and (max-width: 767px) {
  #contacts-page div .mdl-checkbox {
    width: 42%;
    margin-left: 17px;
    margin-top: 10px; }
  #contacts-page h2 {
    margin-left: 17px; } }



