.icon-bar {
  position: fixed;
  top: 40%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  display: none; }
  .icon-bar a {
    display: block;
    text-align: center;
    padding: 16px;
    -webkit-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;
    color: white;
    font-size: 20px; }
    .icon-bar a:hover {
      background-color: #7c7777; }
  .icon-bar .facebook {
    background: #3B5998;
    color: white; }
  .icon-bar .instagram {
    background: #e64980;
    color: white; }
  .icon-bar .linkedin {
    background: #007bb5;
    color: white; }
  .icon-bar .youtube {
    background: #bb0000;
    color: white; }
  .icon-bar .content {
    margin-left: 75px;
    font-size: 30px; }
  @media only screen and (min-width: 600px) {
    .icon-bar {
      display: block; } }



