#about-page #about-us-submenu {
  border-right: 2px solid #000000; }

#about-page .demo-list-item .mdl-list__item-primary-content {
  font-size: 24px; }

#about-page .demo-card-square {
  display: inline-block;
  margin: 15px; }
  #about-page .demo-card-square > .mdl-card__title {
    color: #fff;
    height: 250px; }
  #about-page .demo-card-square .mdl-card__supporting-text h3 {
    height: 70px; }

#about-page .team-members-list .demo-card-square.mdl-card {
  width: 40%;
  height: 500px; }

#about-page .team-members-list .team-member-title {
  margin: 15px; }

#about-page .mdl-card__supporting-text h2 {
  height: 60px; }

#about-page #bojo-card {
  background: url("/images/team-members/bojo.jpg");
  background-size: cover; }

#about-page #didi-card {
  background: url("/images/team-members/didi.jpg");
  background-size: cover; }

#about-page .mdl-dialog {
  width: 700px; }

#about-page .mdl-list__item-primary-content:hover {
  color: #3f51b5; }

@media screen and (min-width: 768px) {
  #about-page #about-us-submenu {
    border-right: none; } }

@media screen and (max-width: 700px) {
  #about-page .team-members-list .demo-card-square.mdl-card {
    width: 90%; } }



